.headerContanier {
  /* position: relative; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
  z-index: 2;
}

.logo {
  color: #fff;
  font-family: Pristina;
  font-size: 60px;
  font-weight: 400;
  margin: 0;
  align-self: start;
  align-self: center;
}

select {
  background-color: #80af81;

  color: #fff;

  border: none;
  outline: none;
  font-weight: 500;
  padding: 5px 10px;
  border-radius: 6px;
}

.darkModeSelection {
  background-color: #3d5f3d;
}

option {
  background-color: #80af81;
  font-weight: 500;
  padding: 13px 5px;
}
.darkModeSelection option {
  background-color: #3d5f3d;
}

body.dark-mode {
  background-color: #121212;
  color: #ffffff;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  border-radius: 15px;
}

.alert {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgb(104 171 60 / 85%);
  padding: 20px;
  box-shadow: 0px 0px 10px rgb(150 241 10 / 61%);
  z-index: 1000;
  border-radius: 8px;
}

.alert p {
  font-family: Poppins;
  font-weight: 500;
  margin-bottom: 10px;
}
.alert button {
  font-family: Poppins;
  font-weight: 500;
  margin-right: 5px;
  padding: 5px 10px;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
}

.langAndMode {
  display: flex;
  justify-content: space-between;
  gap: 15px;
}

.headerContanier .logout {
  color: #fff;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 600;
  padding: 4px 10px;
  border-radius: 16px;
  border: 1px solid #fcfcfc;
  background: rgba(124, 135, 97, 0.67);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 300px) {
  .langAndMode {
    gap: 2px;
  }

  select {
    padding: 5px 1px;
  }

  .headerContanier .logout {
    font-size: 10px;
    padding: 2px 5px;
  }
}

.icon {
  font-size: 24px;
  color: #fff;
  cursor: pointer;
}
